.code-block-add {
  background-color: #e7ffec;
  border: 0px solid lightgreen;
}

.code-block-remove {
  background-color: #ffe4e6;
  border: 0px solid #f1a5a5;
}

.code-block-info {
  background-color: #fafafa;
  color: rgba(0, 0, 0, 0.3);
}

.code-block-standard::before {
  content: "\00a0 ";
}

.importance-CRITICAL {
  color: darkred;
}


.importance-HIGH {
  color: red;
}

.importance-MEDIUM {
  color: orange;
}

.importance-LOW {
  color: rgb(201, 201, 0);
}

.importance-INFORMATIONAL {
  color: green;
}


.status-FAIL {
  color: red;
}

.status-SUCCESS {
  color: green;
}

.status-SKIPPED {
  color: rgb(172, 172, 172);
}